@font-face {
    font-family: 'Roboto';
    src: url('/font/Roboto-Medium.ttf');
}

@font-face {
    font-family: 'Lobster';
    src: url('/font/Lobster_1.3.otf');
}

@font-face {
    font-family: 'Pacifico';
    src: url('/font/Pacifico.ttf');
}

li {
    padding-left :25px;  
    display: inline-block;
    margin-top: 10px;

}

ul{
    height: 50px;  
    text-decoration: none;  
    vertical-align: middle;
    }

ul a{
    text-decoration: none;
    color: white;
    font-size: 1.5em;
    vertical-align: middle;
    font-weight: bold;
}

ul a:hover{
    color:black;
    background-color: white;
}

body{
    background-color: white;
    overflow-x: hidden;
}

.nav{
    background-color: black;
    border-bottom: 4px solid black;
    height: 50px;
    position: fixed;
    width: 100%;
    margin-top: -70px;
    padding: 0px;
    margin-left: -10px;
    margin-right: -10px;
    vertical-align: middle;
    text-align: center;
    -webkit-box-shadow: 0 0 8px #999;
            box-shadow: 0 0 8px #999;
}

#Home{
    width:80%;
    background-color: white;
    margin: 0 auto;
}


#Skills{
    height:100%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    background-color: white;
    
}

.container-presentation{
    margin-top: 20px;
    height : 800px;
    width: 100%;
    background-color: white;
    -ms-flex-align: center;
        align-items: center;
    
}

.container-left-presentation{
    margin-left: 20px;
}

.container-left-presentation-align{
