}

.welcome{
    margin-top: 10px;
    font-size: 1.6em;
    text-align: center;
}

.picture{
    height: 300px;
    width: 250px;
}

.date{
    font-weight: none;
    font-size: 1.4em;
    margin-bottom: 5px;
    margin-top: 5px;
    font-family: "Roboto";
}

.description-enib{
    font-style: italic;
}

h2{
    margin-left: 20px;
    font-size:1.6em;
    color: white;    
    font-family: Roboto;
}

.section-div{
    height : 50px;
    line-height:8px;
    width:100%; 
    background-color: rgb(0,0,0);
}

.name-institution{
    font-size:1.8em;
    font-weight: bold;
