    font-size: 1.6em;
}

#header-presentation{
    width:100%;
    text-align: center;
}

.poster{
    height : 500px;
    width : 100%;
    text-align: center;
    margin: auto;
}

.text-welcome{
    text-align: center;
    font-size: 1.4em;
}


